.header {
  display: grid;
  grid-template-areas: "logo logo nav";
}
.header__logo {
  grid-area: logo;
}
.header__logo .logo {
  max-width: 100%;
  height: auto;
  margin: 0.9375rem;
  vertical-align: middle;
}
@media (min-width: 71.25em) {
  .header__logo {
    margin: 1.25rem;
  }
}
@media (min-width: 71.25em) {
  .header__nav {
    margin: 1.25rem;
  }
}
.header__nav ul {
  vertical-align: middle;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 71.25em) {
  .header__nav ul {
    justify-content: end;
  }
}
.header__nav ul li {
  padding: 0.9375rem;
}
.header__nav ul li :hover {
  color: var(--DesaturatedBlue);
}
@media (min-width: 71.25em) {
  .header__nav ul li {
    padding: 1.25rem;
  }
}
.header__nav ul li a {
  vertical-align: middle;
  font-size: 1.0625rem;
  color: var(--VeryDarkBlue);
}
@media (min-width: 71.25em) {
  .header__nav ul li a {
    font-size: 1.25rem;
  }
}

.first .card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
}
@media (min-width: 71.25em) {
  .first .card {
    flex-direction: row-reverse;
    flex-wrap: nowrap;
    align-items: center;
    margin: 0.9375rem;
  }
}
.first .card__image img {
  max-width: 100%;
  height: auto;
}
@media (min-width: 71.25em) {
  .first .card__test {
    margin: 0.9375rem;
    padding: 20px;
  }
}
.first .card__heading {
  padding: 0.625rem;
  margin: 0.9375rem;
  font-size: 1.1875rem;
  text-align: center;
}
@media (min-width: 71.25em) {
  .first .card__heading {
    font-size: 1.25rem;
    text-align: left;
  }
}
.first .card__explain {
  padding: 0.625rem;
  width: 100%;
  color: var(--DesaturatedBlue);
  font-size: 1.1875rem;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 71.25em) {
  .first .card__explain {
    font-size: 1.125rem;
    text-align: left;
  }
}
.first .card__getstarted form {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
@media (min-width: 71.25em) {
  .first .card__getstarted form {
    flex-direction: row;
    justify-content: left;
  }
}
.first .card__getstarted form input[type=email] {
  align-self: center;
  border: 0.125rem solid var(--DesaturatedBlue);
  border-radius: 0.25rem;
  padding: 0.9375rem;
  width: 25rem;
  margin: 0.9375rem;
  font-size: 1.125rem;
}
.first .card__getstarted form a {
  align-self: inherit;
  border: none;
  border-radius: 0.25rem;
  width: 25rem;
  padding: 0.9375rem;
  text-align: center;
  align-self: center;
  background: var(--BrightBlue);
  color: var(--LightGrayishBlue);
  font-size: 1.125rem;
  font-weight: 700;
}
@media (min-width: 71.25em) {
  .first .card__getstarted form a {
    width: 12.5rem;
  }
}
.first .card__getstarted form a:hover {
  background-color: blue;
}

input[type=email]:invalid {
  border: 2px solid red;
  background: pink;
}

.second {
  grid-area: div2;
}
.second .card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  padding: 1.25rem;
  margin: 1.25rem 0 1.25rem 0;
}
@media (min-width: 71.25em) {
  .second .card {
    justify-content: baseline;
    flex-direction: row-reverse;
    flex-wrap: nowrap;
  }
}
.second .card__image {
  margin-bottom: 3.125rem;
  width: 100%;
}
.second .card__image img {
  max-width: 100%;
  height: auto;
}
@media (min-width: 71.25em) {
  .second .card__test2 {
    display: flex;
    justify-content: left;
    flex-direction: row;
    flex-wrap: wrap;
  }
}
.second .card__heading {
  padding: 0.625rem;
  margin: 0.9375rem;
  font-size: 1.125rem;
  text-align: center;
  color: var(--VeryDarkBlue);
}
.second .card__explain {
  padding: 0.625rem;
  text-align: center;
  color: var(--DesaturatedBlue);
  font-size: 1.125rem;
}
@media (min-width: 71.25em) {
  .second .card__explain {
    text-align: left;
  }
}
.second .card__explain p {
  margin-bottom: 0.9375rem;
}
.second .card__work {
  display: flex;
  justify-content: center;
  flex-direction: row;
}
.second .card__work :hover {
  color: rgba(60, 159, 143, 0.8);
}
.second .card__work a {
  color: var(--ModerateCyan);
  border-bottom: 0.125rem solid var(--ModerateCyan);
  padding: 0.625rem;
  font-size: 1.125rem;
}
.second .card__work a img {
  margin-left: 0.5rem;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
.second .card__comment {
  margin-top: 1.875rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0 0 0.4375rem 0.1875rem #BCBCBC;
  padding: 1.25rem;
  border-radius: 0.3125rem;
  background: var(--LightGrayishBlue);
}
@media (min-width: 71.25em) {
  .second .card__comment {
    margin-right: 3.125rem;
  }
}
.second .card__comment img {
  max-width: 100%;
  height: auto;
  margin-bottom: 0.625rem;
  margin-left: 0.3125rem;
}
.second .card__comment p {
  color: var(--VeryDarkBlue);
  font-size: 1.125rem;
}
.second .card__comment .author {
  display: flex;
  justify-content: baseline;
  padding: 0.9375rem;
}
.second .card__comment .author .box img {
  border-radius: 50%;
  max-width: 100%;
  height: auto;
}
.second .card__comment .author .box2 {
  padding: 0.375rem;
  display: flex;
  justify-content: baseline;
  flex-direction: column;
}
.second .card__comment .author .box2 p {
  margin-bottom: 0.125rem;
  text-align: left;
  font-size: 1rem;
  font-weight: 700;
}
.second .card__comment .author .box2 span {
  color: var(--DesaturatedBlue);
  font-size: 0.8125rem;
  font-weight: 400;
}

.third {
  grid-area: div3;
  background: var(--DesaturatedBlue);
}
.third .card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  padding: 3.125rem 0.9375rem 3.125rem 0.9375rem;
  color: var(--LightGrayishBlue);
}
@media (min-width: 71.25em) {
  .third .card {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}
@media (min-width: 71.25em) {
  .third .card__test3 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 1.25rem;
    margin-right: 3.125rem;
  }
}
.third .card__acces {
  margin-bottom: 0.9375rem;
  font-size: 1.5625rem;
  text-align: center;
}
@media (min-width: 71.25em) {
  .third .card__acces {
    text-align: left;
    padding: 0.9375rem;
  }
}
.third .card__explain {
  margin-bottom: 0.9375rem;
  font-size: 1.25rem;
  padding: 0.625rem;
  text-align: center;
}
@media (min-width: 71.25em) {
  .third .card__explain {
    text-align: left;
    padding: 0.9375rem;
  }
}
.third .card form {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.third .card form input[type=email] {
  align-self: center;
  border: 0.125rem solid var(--DesaturatedBlue);
  border-radius: 0.25rem;
  padding: 0.9375rem;
  width: 25rem;
  margin: 0.9375rem;
  font-size: 1.0625rem;
  text-decoration: none;
}
@media (min-width: 71.25em) {
  .third .card form input[type=email] {
    align-self: baseline;
  }
}
.third .card form a {
  border: none;
  border-radius: 0.25rem;
  width: 25rem;
  padding: 0.9375rem;
  text-align: center;
  align-self: center;
  background: var(--BrightBlue);
  color: var(--LightGrayishBlue);
  font-size: 1.0625rem;
  font-weight: 700;
}
@media (min-width: 71.25em) {
  .third .card form a {
    width: 12.5rem;
    align-self: baseline;
    font-size: 0.9375rem;
    margin-left: 1.0625rem;
  }
}
.third .card form a:hover {
  background: blue;
}

.footer {
  grid-area: footer;
  background: var(--VeryDarkBlue);
  width: 100%;
  padding: 1.5625rem;
}
@media (min-width: 71.25em) {
  .footer {
    display: flex;
    justify-content: baseline;
    flex-wrap: wrap;
    flex-direction: row;
  }
}
@media (min-width: 71.25em) {
  .footer__test4 {
    margin: 1.25rem;
  }
}
.footer__logo {
  margin-bottom: 1.25rem;
}
.footer__logo img {
  color: var(--LightGrayishBlue);
}
.footer__phone {
  font-size: 1.0625rem;
  color: var(--LightGrayishBlue);
}
.footer__phone img {
  max-width: 100%;
  height: auto;
  margin: 0.625rem;
  vertical-align: middle;
}
.footer__mail {
  font-size: 1.0625rem;
  color: var(--LightGrayishBlue);
}
.footer__mail img {
  max-width: 100%;
  height: auto;
  margin: 0.625rem;
  vertical-align: middle;
}
@media (min-width: 71.25em) {
  .footer__nav-first {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 6.25rem;
  }
}
.footer__nav-first ul {
  padding: 0.5625rem;
}
.footer__nav-first ul li {
  padding: 0.3125rem;
}
.footer__nav-first ul li :hover {
  color: var(--DesaturatedBlue);
}
.footer__nav-first ul li a {
  font-size: 1.25rem;
  color: var(--LightGrayishBlue);
}
@media (min-width: 71.25em) {
  .footer__nav-second {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 6.25rem;
  }
}
.footer__nav-second ul {
  padding: 0.5625rem;
}
.footer__nav-second ul li {
  padding: 0.3125rem;
}
.footer__nav-second ul li :hover {
  color: var(--DesaturatedBlue);
}
.footer__nav-second ul li a {
  font-size: 1.25rem;
  color: var(--LightGrayishBlue);
}
.footer__social {
  text-align: center;
  padding: 1.875rem;
}
@media (min-width: 71.25em) {
  .footer__social {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 3.125rem;
  }
}
.footer__social .facebook {
  margin: 0.625rem;
}
.footer__social .twitter {
  margin: 0.625rem;
}
.footer__social .instagram {
  margin: 0.625rem;
}
.footer .attribution {
  text-align: center;
  color: var(--LightGrayishBlue);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  font-family: var(--font-open-sans);
  background: var(--LightGrayishBlue);
}

:root {
  --VeryDarkBlue: hsl(243, 87%, 12%);
  --DesaturatedBlue: hsl(238, 22%, 44%);
  --BrightBlue: hsl(224, 93%, 58%);
  --ModerateCyan: hsl(170, 45%, 43%);
  --LightGrayishBlue: hsl(240, 75%, 98%);
  --LightGray: hsl(0, 0%, 75%);
}

:root {
  --font-raleway: "Raleway", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;
}

@media (min-width: 71.25em) {
  .container {
    padding: 0;
  }
}

h1, h2, h3 {
  font-family: var(--font-raleway);
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  color: var(--VeryDarkBlue);
  font-weight: 700;
}

a, a:visited, a:active {
  text-decoration: none;
}

ul {
  list-style-type: none;
}